* {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
	background: transparent;
	font-weight: normal;
	text-decoration: none;
	outline: none;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	transition: 400ms;
}
ol,
ul {
	list-style: none;
}
del {
	text-decoration: line-through;
}
body {
	font-family: "Gordita Regular", "Gordita Medium";
	font-size: 17px;
	color: #5b5a5d;
	overflow-x: hidden;
	min-width: 320px;
	font-weight: 500;
	background: #141414;
}
input,
textarea,
select {
	font-family: "Gordita Regular", "Gordita Medium";
	font-weight: 500;
}

a:hover,
.submit:hover {
	filter: alpha(opacity = 85);
	-moz-opacity: 0.85;
	-khtml-opacity: 0.85;
	opacity: 0.85;
}
.thumb {
	display: block;
}
.thumb img {
	display: block;
	width: 100%;
}
p {
	line-height: 1.6em;
	font-size: 16px;
	font-family: "GT-Eesti", Arial;
	font-weight: 500;
	color: #42526e;
}
.hidden {
	display: none;
}
br.clear {
	clear: both;
	line-height: 0;
}
.wrapper {
	width: 90%;
	margin: 0 auto;
	max-width: 1200px;
}

@font-face {
	font-family: "Gordita Regular";
	font-style: normal;
	font-weight: normal;
	src: local("components/Assets/fonts/Gordita Regular"),
		url("components/Assets/fonts/Gordita Regular.woff") format("woff");
}

@font-face {
	font-family: "Gordita Medium";
	font-style: normal;
	font-weight: normal;
	src: local("components/Assets/fonts/Gordita Medium"),
		url("components/Assets/fonts/Gordita Medium.woff") format("woff");
}

header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 50px 0;
	height: 80px;
	position: relative;
}
header h1 {
	width: 20%;
}
header h1 img {
	display: block;
	width: 100%;
}
header nav.desktop ul {
	display: flex;
	align-items: center;
}
header nav.desktop ul li {
	margin-right: 15px;
	cursor: pointer;
	text-transform: capitalize;
	font-size: 14px;
	font-weight: bold;
}
header nav.desktop ul li:hover {
	color: #fff;
}
header nav.desktop ul li:last-child {
	margin-right: 0;
	background-image: linear-gradient(to right, #fb5200 10%, #a14d9b 80%);
	padding: 1px;
	border-radius: 10px;
}
header nav.desktop ul li a {
	display: inline-block;
	padding: 12px 24px;
	background: #141414;
	border-radius: 10px;
	color: #fb5200;
	box-sizing: border-box;
	text-transform: capitalize;
	font-size: 14px;
	font-weight: bold;
}
header h2 {
	display: none;
	background-image: linear-gradient(to right, #fb5200 10%, #a14d9b 80%);
	padding: 1px;
	border-radius: 10px;
	position: absolute;
	right: 0;
}
header h2 a {
	display: inline-block;
	padding: 12px 24px;
	background: #141414;
	border-radius: 10px;
	color: #fb5200;
	box-sizing: border-box;
	text-transform: capitalize;
	font-size: 14px;
	font-weight: bold;
}
header nav.mobile {
	display: none;
	z-index: 7;
	position: absolute;
	top: 90px;
	width: 100%;
}
header nav.mobile ul {
	display: flex;
	flex-direction: column;
	background: #141414;
}
header nav.mobile ul li {
	padding: 10px 0 15px 15px;
	color: #a14d9b;
	text-transform: capitalize;
}
header nav.mobile ul li:hover {
	color: #fb5200;
}
header nav.mobile ul li:active {
	color: #fb5200;
}
#spotlight {
	position: relative;
	padding-top: 50px;
	margin-bottom: 50px;
}
#spotlight div.left {
	width: 50%;
}
#spotlight div.left h2 {
	font-size: 56px;
	font-family: "Gordita Medium";
	text-transform: capitalize;
	color: #fff;
}
#spotlight div.left h2 small {
	font-size: 56px;
	text-transform: capitalize;
	font-family: "Gordita Medium";
	color: #fb5200;
}
#spotlight div.left p {
	margin-top: 30px;
	color: #5b5a5d;
	font-family: "Gordita Medium";
	font-size: 20px;
	max-width: 450px;
	line-height: 2rem;
}
#spotlight div.left a {
	background-image: linear-gradient(to right, #fb5200 10%, #a14d9b 80%);
	display: inline-block;
	padding: 20px 34px;
	border-radius: 10px;
	margin-top: 30px;
	color: #fff;
	font-size: 16px;
	text-align: center;
}
#spotlight div.left a span {
	display: block;
	background: inherit;
	color: #efbbc5;
}
#spotlight div.bg1 {
	width: 18%;
	z-index: 2;
	position: absolute;
	top: 57%;
	right: 33%;
}
#spotlight div.bg1 img {
	display: block;
	width: 100%;
	filter: blur(25px);
}
#spotlight div.bg2 {
	width: 18%;
	z-index: 1;
	position: absolute;
	top: 5%;
	right: 3%;
}
#spotlight div.bg2 img {
	display: block;
	width: 100%;
	filter: blur(25px);
}
#spotlight div.container {
	position: absolute;
	z-index: 5;
	width: 40%;
	top: 1%;
	right: 1%;
}
#spotlight div.container img {
	display: block;
	width: 100%;
}
#awards {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	padding-top: 80px;
}
#awards div.box {
	width: 23%;
	background-image: linear-gradient(to right, #fb5200 10%, #a14d9b 80%);
	padding: 1px;
	border-radius: 10px;
}
#awards div.box div.border {
	padding: 2px;
	background: #141414;
	border-radius: 10px;
}
#awards div.box:hover {
	transform: scaleY(1.05);
	cursor: pointer;
}
#awards div.box div.border:hover {
	background-image: linear-gradient(to right, #fb5200 10%, #a14d9b 80%);
}
#awards div.box div.border:hover div.content div.head div.container {
	background: #141414;
}
#awards div.box div.border:hover div.content span {
	color: #fff;
}
#awards div.box div.border div.content {
	padding: 20px 30px;
}
#awards div.box div.border div.content span {
	display: block;
	margin-top: 30px;
	text-transform: capitalize;
	font-size: 14px;
	font-weight: bold;
}
#awards div.box div.border div.content div.head {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
#awards div.box div.border div.content div.head div.container {
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 10px;
	background-color: #5b5a5d;
}
#awards div.box div.border div.content div.head div.container img {
	width: 50%;
	display: block;
}
#awards div.box div.border div.content div.head span {
	margin-top: 0;
	font-size: 14px;
}
#awards div.box div.border div.content h5 {
	margin: 20px 0;
	font-size: 16px;
	color: #fff;
	line-height: 2rem;
}
#awards div.box div.border div.content div.foot {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 20px;
}
#awards div.box div.border div.content div.foot span {
	margin-top: 0;
}
#contact {
	margin-top: 50px;
	position: relative;
	padding-top: 50px;
}
#contact img {
	display: block;
	width: 100%;
}
#contact div.content {
	width: 45%;
	position: absolute;
	top: 25%;
	left: 5%;
}
#contact div.content h3 {
	font-size: 38px;
	font-family: "Gordita Medium";
	color: #fff;
}
#contact div.content p {
	margin-top: 20px;
	font-size: 21px;
	font-family: "Gordita Medium";
	color: #efbbc5;
}
#contact div.content form {
	width: 75%;
	background: #fff;
	margin-top: 30px;
	border-radius: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}
#contact div.content form input[type="email"] {
	padding: 20px;
}
#contact div.content form input[type="submit"] {
	padding: 20px;
	color: #fff;
	background-image: linear-gradient(to right, #fb5200 10%, #a14d9b 80%);
	border-radius: 10px;
}
footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	padding: 50px 0;
}
footer div.left {
	width: 35%;
}
footer div.left h5 {
	text-transform: capitalize;
	line-height: 1.5rem;
}
footer div.right {
	width: 50%;
	display: flex;
	flex-wrap: wrap;
}
footer div.right ul {
	display: flex;
	align-items: center;
	width: 60%;
}
footer div.right ul li {
	margin-right: 15px;
	text-transform: capitalize;
	cursor: pointer;
	font-size: 16px;
}
footer div.right ul li:hover {
	color: #fff;
}
footer div.right span {
	display: inline-block;
	width: 40%;
	text-align: right;
	position: relative;
}
footer div.right span::before {
	content: "";
	position: absolute;
	left: 0%;
	bottom: 0;
	top: -40%;
	width: 2px;
	height: 60px;
	background: #42526e;
}
footer div.right span small {
	cursor: pointer;
	color: #fb5200;
}
@media all and (max-width: 1280px) {
	#awards div.box div.border div.content {
		padding: 20px 20px;
	}
	#awards div.box div.border div.content div.head div.container {
		width: 40px;
		height: 40px;
	}
	#contact div.content {
		width: 55%;
	}
	footer div.right {
		width: 65%;
	}
}
@media all and (max-width: 980px) {
	#spotlight div.left h2 {
		font-size: 36px;
	}
	#spotlight div.bg1 {
		top: 38%;
	}
	#awards div.box div.border div.content div.head span {
		font-size: 12px;
	}
	#awards div.box div.border div.content div.head div.container {
		width: 30px;
		height: 30px;
	}
	#awards div.box div.border div.content h5 {
		font-size: 14px;
	}
	#awards div.box div.border div.content div.foot span {
		font-size: 11px;
	}
	#contact div.content {
		width: 85%;
	}
	footer div.right ul li {
		font-size: 13px;
	}
	footer div.right span {
		font-size: 13px;
	}
	footer div.right span::before {
		height: 40px;
	}
}
@media all and (max-width: 768px) {
	#spotlight div.left h2 {
		font-size: 30px;
	}
	#spotlight div.left p {
		font-size: 16px;
	}
	#awards div.box {
		width: 48%;
		margin-bottom: 20px;
	}
	#contact {
		margin-top: 0;
	}
	#contact div.content p {
		font-size: 16px;
	}
	#contact div.content h3 {
		font-size: 28px;
	}
	footer div.right {
		width: 100%;
		margin-top: 30px;
	}
	footer div.right ul {
		width: 50%;
	}
}
@media all and (max-width: 640px) {
	header h1 {
		width: 50%;
	}
	header h2 {
		display: block;
	}
	header nav.desktop {
		display: none;
	}
	header nav.mobile {
		display: block;
	}
	#spotlight div.left {
		width: 100%;
	}
	#spotlight div.container {
		top: 75%;
	}
	#spotlight div.left a {
		padding: 12px 24px;
		font-size: 14px;
	}
	#awards div.box div.border div.content {
		padding: 20px 18px;
	}
	#contact div.content {
		position: unset;
		width: 100%;
		padding-top: 20px;
	}
	#contact div.content form {
		width: 100%;
	}
	footer div.left {
		width: 100%;
	}
	footer div.left h2 {
		text-align: center;
	}
	footer div.left h5 {
		text-align: center;
	}
	footer div.right ul {
		justify-content: center;
		width: 100%;
	}
	footer div.right span {
		width: 100%;
		margin-top: 20px;
		text-align: center;
	}
	footer div.right span::before {
		content: unset;
	}
	@media all and (max-width: 480px) {
		#spotlight div.container {
			display: none;
		}
		#spotlight div.left a {
			display: block;
			font-size: 16px;
		}
		#awards {
			padding-top: 20px;
		}
		#awards div.box {
			width: 100%;
		}
		#contact img {
			display: none;
		}
		#contact div.content {
			padding-top: unset;
		}
		#contact div.content form input[type="email"] {
			width: 50%;
		}
	}
}
